.container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4em 3em 0 !important;
  column-gap: 2em;
}

.left {
  display: flex;
  flex-basis: 49%;
  flex-direction: column;
  justify-content: center;
}

.left .section1 {
  color: #ff914d;
  font-weight: 500;
  letter-spacing: 0.5px;
  font-size: 1.2em;
}

.left .section2 {
  color: black;
  margin: 0 !important;
  font-size: 3.5em;
  font-weight: 500;
  line-height: 1.2;
}

.left .section3 {
  margin-top: 1em;
  font-size: 1.3em;
  color: #212529;
  text-align: justify;
}

.left .section4 {
  font-weight: 500;
  margin-top: 1em !important;
  /* font-size:1.3em; */
}

.left .section4 a:hover {
  color: #0056b3;
}

.left .btns {
  margin-top: 2em !important;
  display: flex;
  align-items: center;
  column-gap: 1em;
  width: 80%;
}

.left .btn1 {
  flex: 1;
  text-align: center;
  background-color: #ff914d;
  color: white;
  padding: 1em 2em !important;
  border-radius: 10px;
  font-weight: 500;
  cursor: pointer;
  border: 2px solid #ff914d;
  transition: transform 250ms;
}

.left .btn1:hover {
  background-color: white;
  color: #ff914d;
  transform: scale(1.05);
}

.left .btn2 {
  flex: 1;
  text-align: center;
  border: 2px solid #ff914d;
  color: #ff914d;
  font-weight: 500;
  padding: 1em 2em !important;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 250ms;
}

.left .btn2:hover {
  background-color: #ff914d;
  color: white;
  transform: scale(1.05);
}

.right {
  display: flex;
  flex-basis: 51%;
}

.right .image {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 612px) {
  html {
    font-size: 10px;
  }
  .container {
    flex-direction: column-reverse;
    row-gap: 5em;
  }
  .left {
    align-items: center;
    text-align: center;
  }
}
